import React, { useEffect, useState } from "react"
import "./Workflow.scss"
import Heading from "../../shared/Heading"
import { Grid } from "@mui/material"

const dataCol = [
  {
    id: 1,
    number: "01",
    header: "Understanding",
    top: true,
    subText: "Understanding business needs and technical requirements",
    text: "Firstly, our data engineering team carries out the workshops and discovery calls with potential end-users. Then, we get all the necessary information from the technical departments.",
  },
  {
    id: 2,
    number: "02",
    header: "Analysing",
    top: false,
    subText: "Analysis of existing and future data sources",
    text: "At this stage, it is essential to go through current data sources to maximize the value of data. You should identify multiple data sources from which structured and unstructured data may be collected. During this step, our experts will prioritize and assess them.",
  },
  {
    id: 3,
    number: "03",
    header: "Building and implementing",
    top: true,
    subText: "Building and implementing Data Lake and Pipelines",
    text: "Data Lakes are the most cost-effective alternatives for storing data. A data lake is a data repository system that stores raw and processed structured and unstructured data files. A system like stores flat, source, transformed, or raw files.",
  },
  {
    id: 4,
    number: "04",
    header: "Automation and deploying",
    top: false,
    subText: "Automation and deployment",
    text: "The next step is one of the most important parts in data development consulting – DevOps. Our team develops the right DevOps strategy to deploy and automate the data pipeline.",
  },
  {
    id: 5,
    number: "05",
    header: "Testing",
    top: true,
    subText: "Testing",
    text: "Testing, measuring, and learning — are important at the last stage of the Data Engineering Process. DevOps automation is vital at this moment. Data Lakes could be established or accessed, using specific tools such as Hadoop or s3.",
  },
]

export default function CustomSoftware() {
  const [counts, setCounts] = React.useState(1)
  const [innerheight, setInnerHeight] = useState(429)
  const [heights, setHeights] = useState({
    screenHight: 200,
    dataEngStepsHeight: 200,
  })
  const [state, setState] = React.useState({
    id: 1,
    number: "01",
    header: "Understanding",
    top: true,
    subText: "Understanding business needs and technical requirements",
    text: "Firstly, our data engineering team carries out the workshops and discovery calls with potential end-users. Then, we get all the necessary information from the technical departments.",
  })

  const [width, setWidth] = useState(null)

  const updateDimensions = () => {
    if (typeof window !== "undefined") {
      setWidth(window.innerWidth)
      setInnerHeight(window.innerHeight)
    }
  }
  useEffect(() => {
    if (typeof window !== "undefined") {
      setWidth(window.innerWidth)
      setInnerHeight(window.innerHeight)
    }
  }, [])
  useEffect(() => {
    if (window !== undefined) {
      window.addEventListener("resize", updateDimensions)
      return () => window.removeEventListener("resize", updateDimensions)
    }
  }, [width])

  useEffect(() => {
    const element = document.querySelector(".data-eng-steps")
    const elementClientHeight = element.clientHeight
    setHeights({
      screenHight: innerheight,
      dataEngStepsHeight: elementClientHeight,
    })
  }, [innerheight])

  useEffect(() => {
    var timer = null
    var lastScroll = 0
    var direction = "down"

    const handleScroll = function () {
      const element = document.querySelector(".data-eng-steps")
      let elementClientHeight = element.clientHeight
      let deviceHeight = window.innerHeight

      if (window.scrollY < lastScroll) {
        direction = "up"
      } else if (window.scrollY > lastScroll) {
        direction = "down"
      }
      setTimeout(() => {
        lastScroll = window.scrollY
      }, 100)

      console.log("top in scroll", deviceHeight - elementClientHeight + 80)

      if (
        element &&
        element.getBoundingClientRect().top ===
          deviceHeight - elementClientHeight + 80
      ) {
        if (timer !== null) {
          clearTimeout(timer)
        }
        timer = setTimeout(function () {
          if (direction === "up") {
            setCounts(c => {
              if (c > 1) {
                return c - 1
              }
              return c
            })
          } else {
            setCounts(c => {
              if (c < 5) {
                return c + 1
              }
              return c
            })
          }
        }, 50)
      }
    }
    window.addEventListener("scroll", handleScroll, false)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  useEffect(() => {
    const currentOption = dataCol.find(tab => tab.id === counts)
    setState(currentOption)
  }, [counts])

  console.log({
    topinElement: heights.screenHight - heights.dataEngStepsHeight + 80,
  })

  return (
    <div className="data-eng-parent-sticky">
      <div
        className="data-eng-section-sticky"
        style={{
          top: `${heights.screenHight - heights.dataEngStepsHeight + 80}px`,
        }}
      >
        <div className="data-eng-steps">
          <div className="quick-project-inner">
            <div className="">
              <div className="col-md-12 header-section">
                <div
                  className="container"
                  // data-aos="fade-right"
                  // data-aos-delay="350"
                  // data-aos-duration="1000"
                >
                  <Heading variant="40" className="header">
                    Check out how we help businesses all over the world make the
                    most of their data
                  </Heading>
                </div>
              </div>
              <div className="time-line-wrapper">
                <div className="col-md-12 d-flex py-5 container">
                  {width >= 650 &&
                    dataCol.map(item => {
                      return (
                        <div
                          key={item.id}
                          // data-aos="fade-up"
                          // data-aos-anchor-placement="top-bottom"
                          // data-aos-duration="1000"
                          // data-aos-delay="300"
                          style={{ width: `${100 / dataCol.length}%` }}
                        >
                          {item.top && (
                            <div
                              className={
                                item.id <= state.id
                                  ? "time-line-top active-top"
                                  : "time-line-top"
                              }
                              key={item.id}
                              onClick={() => setState(item)}
                            >
                              <div className="wrapper">
                                <div className="circle">
                                  <p>{item.number}</p>
                                </div>
                                <p className="header">{item.header}</p>
                                <div className="triangle-top" />
                              </div>

                              <div className="divider"></div>
                              {/* <p className="text">{item.text}</p> */}
                            </div>
                          )}
                          {!item.top && (
                            <div
                              className={
                                item.id <= state.id
                                  ? "time-line-bottom active-bottom"
                                  : "time-line-bottom"
                              }
                              onClick={() => setState(item)}
                              key={item.id}
                            >
                              <div className="wrapper">
                                {/* <p className="text">{item.text}</p> */}
                              </div>
                              <div className="divider"></div>
                              <div className="triangle-bottom" />
                              <p className="header">{item.header}</p>
                              <div className="circle">
                                <p>{item.number}</p>
                              </div>
                            </div>
                          )}
                        </div>
                      )
                    })}

                  {width < 650 && (
                    <div
                      data-aos="fade-up"
                      data-aos-anchor-placement="top-bottom"
                      data-aos-duration="1000"
                    >
                      <Grid container className="CSD-mobile">
                        {dataCol.map(item => {
                          return (
                            <Grid
                              container
                              direction={item.top ? "row" : "row-reverse"}
                              mt={6}
                              key={item.id}
                            >
                              <Grid item sm={12} xs={12}>
                                <div className="inner-div">
                                  <div className="circle">
                                    <p>{item.number}</p>
                                  </div>
                                  <p className="header">{item.header}</p>
                                </div>
                              </Grid>
                              <Grid
                                item
                                sm={12}
                                xs={12}
                                className="d-flex justify-content-center align-items-center flex-column"
                              >
                                {/* <p className="sub-text">{item.subText}</p> */}
                                <p className="text">{item.text}</p>
                              </Grid>
                            </Grid>
                          )
                        })}
                      </Grid>
                    </div>
                  )}
                </div>
                {width > 650 && (
                  <div className="desc-wrapper container">
                    <p className="header">{state.subText}</p>
                    {/* <p className="sub-text">{state.subText}</p> */}
                    <p className="text">{state.text}</p>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
